export default{
	
	data(){
		return{
			theMid:'',
			showNewsData: [
				{
					money:'100.00',
					time:1593571037
				},
				{
					money:100.00,
					time:1593571037
				},
				{
					money:100.00,
					time:1593571037
				},
				{
					money:100.00,
					time:1593571037
				}
			],
		}
	},
	
	onLoad(eee) {
		
		var _this = this;
		uni.getStorage({
			key: 'mid',
			success: function(res) {
				console.log('getStoragegemid' + res.data);
				_this.theMid = res.data;
				_this.getServicList();
			}
		});
		this.getServicList();
	},
	methods:{
		
		
		async getServicList(){
			
			var serverUrl="Home/Message/getList";
			var data={
				mid:this.theMid,
				type:this.currentIndex,
				limit:10,
			    page:this.page
			}
			var res = await this.requestFromServer({
				url:serverUrl,
				data:data
			})
			if(this.page==1){
				this.showNewsData=[];
			}
			uni.stopPullDownRefresh();
			if(res.data){
				this.showNewsData=this.showNewsData.concat(res.data);
				if(res.data.length<10){
					this.loadingText='没有更多数据了';
				}else{
					this.loadingText='加载更多数据...';
				}
			}else{
				this.loadingText='没有更多数据了';
			}
			
		},
		
		
		
		dateFormat:function(time) {
			return this.getDateSecond(time);
		},
	}
}
